.contact_container
{
    display: grid;
    place-items: center;
}

.contact_wrapper
{
    width: fit-content;
    display: flex;
    gap: 2rem;
}

.contact_wrapper a
{
    background: var(--color-primary);
    font-size: 2rem;
    border: 2px solid transparent;
    transition: var(--transition);
    padding: 2rem;
    border-radius: 1.5rem;
}

.contact_wrapper a:hover
{
    background: transparent;
    border-color: var(--color-gray-100);
}

/* med screen */
@media screen and (max-width: 1024px) {
    
}

/* small screen */
@media screen and (max-width: 600px) {
    .contact_wrapper
    {
        gap: 1.5rem;
    }

    .contact_wrapper a
    {
        padding: 1rem;
        font-size: 1.2rem;
        border-radius: 1rem;
    }
}