.about_section-image
{
    border-radius: 50%;
    overflow: hidden;
    transform: skew(15deg);
    transition: var(--transition);
}

.about_section-image:nth-child(2)
{
    transform: skew(-15deg);
}

.about_section-image:hover
{
    transform: skew(0);
}

.about_section-content h1
{
    margin-bottom: 2rem;
}

.about_section-content p
{
    margin-bottom: 1rem;
}

.about_story-container, .about_mission-container
{
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 10%;
}

.about_Vision-container
{
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 10%;
}


@media screen and (max-width: 1024px) {
    .about_section-image
    {
        width: 60%;
    }

    .about_section-content h1
    {
        margin-bottom: 1.2rem;
    }

    .about_mission-container, .about_Vision-container, .about_story-container
    {
        grid-template-columns: 1fr;
        gap: 3rem;
    }

    .about_Vision.about_section-image
    {
        grid-row: 1;
    }
}

@media screen and (max-width: 600px) {
    .about_section-image
    {
        width: 80%;
        margin-inline: auto;
    }

    .about_mission-container, .about_Vision-container, .about_story-container
    {
        gap: 2rem;
    }
}